@tailwind base;
@tailwind components;
@tailwind utilities;

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
html{
    scroll-behavior:smooth;
}
@media (max-width:850px) {
    html{
        font-size: 13px;
    }
}
#hi{
    /* transform: rotate(180deg); */
    animation: waveHand 1s ease-in-out infinite alternate;
    margin-bottom: 2px;
}

.img{
    animation: rotate 4s ease infinite alternate;
}
#Experience::after{
    content: "";
    /* background-color: rgb(17, 24, 39); */
    border-left: 2px dashed purple;
    width: 5px;
    height: 100%;
    position: absolute;
    left: 50%;
    top: 0;
}
/* Animations  */
@keyframes waveHand {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(80deg);
    }
}

@keyframes rotate{
    0%{
        transform:rotate(0deg)
    }
    100%{
        transform:rotate(360deg)
    }
}


@keyframes leftIn{
    0%{
        transform: translateX(-50px);
        opacity: 0;
    }
    100%{
        transform: translateX(0px);
        opacity: 1;
    }
}

@keyframes rightIn{
    0%{
        transform: translateX(50px);
        opacity: 0;
    }
    100%{
        transform: translateX(0px);
        opacity: 1;
    }
}

@keyframes downIn{
    0%{
        transform: translateY(50px);
        opacity: 0;
    }
    100%{
        transform: translateY(0px);
        opacity: 1;
    }
}

@keyframes upIn{
    0%{
        transform: translateY(-50px);
        opacity: 0;
    }
    100%{
        transform: translateY(0px);
        opacity: 1;
    }
}


.max{
    max-width: 1200px;
}